import React from 'react';

let card = (props) => {
    return (      
            <div className="card">
               <img  src={props.poster} alt="Movie Poster" /> 
              
                <div className="card_info">
                    
                    <span className="card_cat">{props. genres}</span>
                    <h3 className="card_title">{props. movie}</h3>
                    <a href={props.url} target="_blank"><button> WATCH NOW</button></a>
                </div>
            </div>    
    )
}

export default card;